<template>
    <div class="singleBlog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <p>分类：</p>
        <ul>
            <li v-for="category in blog.categories">
                {{category}}
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: 'singleBlog',
        data() {
            return {
                id:null,
                blog:{},
            }
        },
        methods:{

        },
        created(){
            this.$http.get('https://wd5428820307kktbij.wilddogio.com/posts/'+this.$route.params.id+'.json').then(response=>{
                // console.log(response)
                // this.blog = response.data
                return response.data
            }).then(response=>{
                this.blog = response
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .singleBlog{
        max-width: 960px;
        margin: 0 auto;
        padding: 20px;
        background: #eee;
        border: 1px dotted #aaa;
    }
</style>


